import { useState } from "react";
import "./styles.css";
import { Card } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
export default function App() {
  const [d, sd] = useState([{ na: "kan" }]);
  const append = (v) => {
    sd([...d, v]);
  };
  return (
    <div className="App">
      <Append append={append} />
      <button
        disabled={d.length === 1}
        onClick={() => sd(d.slice(0, d.length - 1))}
      >
        Undo
      </button>
      <>
        <Card>
          <Card.Body>{d && d.map((i) => <Display da={i} />)}</Card.Body>
        </Card>
      </>
    </div>
  );
}
const Display = ({ da }) => {
  return (
    <table>
      <thead>
        <tr>
          <th>Name</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{da.na}</td>
        </tr>
      </tbody>
    </table>
  );
};
const Append = ({ append }) => {
  const [na, sna] = useState("");
  const hs = (e) => {
    e.preventDefault();
    append({ na });
    sna("");
  };
  return (
    <form onSubmit={(e) => hs(e)}>
      <input type="text" value={na} onChange={(e) => sna(e.target.value)} />
      <button disabled={na.length === 0} type="submit">
        Append
      </button>
    </form>
  );
};
